import { useState } from "react";

const data = {
  objectives: [
    [
      { name: "stage1", label: " I " },
      { name: "stage2", label: " II " },
      { name: "secret", label: " " },
    ],
    [
      { name: "status", label: " " },
      { name: "action", label: " " },
      { name: "agenda", label: " " },
    ],
    [
      { name: "main", label: " " },
      { name: "pok", label: " " },
      { name: "omega", label: "-" },
    ],
  ],
  relics: [
    [
      { name: "pok", label: " " },
      { name: "codex2", label: " II" },
      { name: "codex4", label: " IV" },
      { name: "te", label: "Thunder's Edge" },
    ],
  ],
};

const defFilters = {
  objectives: {
    status: true,
    action: true,
    agenda: true,
    stage1: true,
    stage2: true,
    secret: true,
    main: true,
    pok: true,
    omega: true,
  },
  relics: {
    pok: true,
    codex2: true,
    codex4: true,
    te: true
  },
};

const useFilter = ({ type, initialFilters }) => {
  const [filters, setFilters] = useState({
    ...defFilters[type],
    ...initialFilters,
  });

  const handler = (name) => {
    setFilters(
      (filters) => ({
        ...filters,
        [name]: !filters[name],
      }),
      []
    );
  };
  return [filters, handler, data[type]];
};

export const useObjectivesFilter = (initialFilters) =>
  useFilter({ type: "objectives", initialFilters });

export const useRelicsFilter = (initialFilters) =>
  useFilter({ type: "relics", initialFilters });
